import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import { Home } from "styled-icons/boxicons-solid/Home"
import { SearchAlt2 as Search } from "styled-icons/boxicons-regular/SearchAlt2"
import { UpArrowAlt as Arrow } from "styled-icons/boxicons-regular/UpArrowAlt"

const MenuBarWrapper = styled.aside`
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: space-between;
  padding: 0.8rem 0;
  position: fixed;
  right: 0;
  width: 3.75rem;
  background: #f0f0f3;
  border-left: 1px solid #dedede;
  top: 0;
`

const MenuBarGroup = styled.div`
  display: flex;
  flex-direction: column;
`

const MenuBarLink = styled(Link)`
  display: block;
`

const MenuBarItem = styled.span`
  color: #555;
  cursor: pointer;
  display: block;
  height: 3.75rem;
  padding: 1.1rem;
  position: relative;
  width: 3.75rem;
  &:hover {
    color: #1fa1f2;
  }
`

const MenuBar = () => (
  <MenuBarWrapper>
    <MenuBarGroup>
      <MenuBarLink to="/" title="Voltar para Home">
        <MenuBarItem>
          <Home />
        </MenuBarItem>
      </MenuBarLink>
      <MenuBarLink to="/search/" title="Pesquisar">
        <MenuBarItem>
          <Search />
        </MenuBarItem>
      </MenuBarLink>
    </MenuBarGroup>
    <MenuBarGroup>
      <MenuBarItem title="Ir para o Topo">
        <Arrow />
      </MenuBarItem>
    </MenuBarGroup>
  </MenuBarWrapper>
)

export default MenuBar