import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import PropTypes from "prop-types"

const PostItemLink = styled(Link)`
  color: #8899a6;
  display: inline-block;
  width: 31%;
  padding: 20px;
  margin: 1%;
  box-sizing: border-box;
  border: 1px solid #eee;
  text-decoration: none;
  &:hover {
    color: #1fa1f2;
  }
`

const PostItemWrapper = styled.section`
  align-items: center;
  display: inline-block;
  width: 100%;
`

const PostItemInfo = styled.div`
  display: inline-block;
`

const PostItemDate = styled.time`
  font-size: 0.9rem;
  margin-top: 15px;
  display: inline-block;
`

const PostItemTitle = styled.h1`
  font-size: 1.5rem;
  font-weight: 700;
  margin: 20px 0;
`

const PostItemDescription = styled.p`
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.2;
`

const PostItem = ({ slug, image, titleImage, date, timeToRead, title, description }) => (
  <PostItemLink to={slug}>
    <PostItemWrapper>
      <img width="100%" src={image} alt={titleImage} />
      <PostItemInfo>
        <PostItemDate>{date} • {timeToRead} min de leitura</PostItemDate>
        <PostItemTitle>
          {title}
        </PostItemTitle>
        <PostItemDescription>
          {description}
        </PostItemDescription>
      </PostItemInfo>
    </PostItemWrapper>
  </PostItemLink>
)

PostItem.propTypes = {
  slug: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  titleImage: PropTypes.string.isRequired,
  date: PropTypes.string.isRequired,
  timeToRead: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired
}

export default PostItem